.widgetTest {
  position: fixed;
  bottom: 0px;
  left: 0px;
  margin: 30px;
  z-index: 99;
}

.calc {
  display: flex;
  flex-direction: column;
  min-height: 400px;
  width: 300px;
  background-color: white;
  border-radius: 10px;
  z-index: 99;
  position: fixed;
  bottom: 0px;
  left: 0px;
  margin: 50px 100px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  padding: 20px;

  span {
    font-size: 12px;
  }

  .select {
    // border: 1px solid rgb(220, 220, 220);
    border: none;
    background-color: rgb(237, 237, 237);
    border-radius: 10px;
    height: 50px;
    width: 100%;
    margin-top: 10px;
    font-family: 'iransansx';
  }

  .input {
    height: 40px;
    border: 1px solid rgb(220, 220, 220);
    width: 96%;
    margin: 7px 0;
    border-radius: 10px;
    padding: 5px;
  }

  .swap {
    margin: 15px 0;
    display: flex;
    align-items: center;
    justify-content: center;

    div {
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 20px;
      background-color: #1876d1;
      width: 30px;
      height: 30px;

      img {
        height: 20px;
        width: 20px;
      }
    }
  }

  .paymentPrice {
    margin-top: 10px;
  }

  .pay {
    border: 1px solid #1876d1;
    background-color: white;
    color: white;
    padding: 10px;
    margin-top: 20px;
    border-radius: 10px;
    cursor: pointer;
    color: #1876d1;
    transition: 0.3s;

    &:hover {
      background-color: #1876d1;
      color: white;
    }
  }
}
