.container {
  color: white;
  background-color: #1876d1;
  // background-color: #00aa95;
  display: flex;
  flex-direction: row;
  height: 80px;
  padding: 0 50px;
  align-items: center;

  .appLogo {
    cursor: pointer;
    margin-left: 10px;
    img {
      height: 50px;
      width: 50px;
    }
  }
  .appName {
    cursor: pointer;
  }
  .menu {
    display: flex;
    flex-direction: row;
    margin-right: 20px;

    .item {
      cursor: pointer;
      margin: 0 15px;
      transition: 0.4s;

      .content {
        background-color: white;
        color: black;
        display: flex;
        flex-direction: row;
        padding: 10px 20px;
        margin-top: 10px;
        position: absolute;
        // width: 350px;
        border-radius: 10px;
        margin-top: 20px;

        .items {
          display: flex;
          flex-direction: column;
          width: 200px;

          .item {
            display: flex;
            flex-direction: column;
            color: black;
            margin: 10px 0;
            a {
              color: black;
              &:hover {
                color: #1876d1;
              }
            }
            span {
              font-size: 12px;
            }
          }
        }

        .image {
          img {
            height: 250px;
            width: 250px;
          }
        }
      }

      .menuItem {
        display: flex;
        flex-direction: row;
        align-items: center;
        text-decoration: none;
        color: white;
        transition: 0.3s;

        &:hover {
          color: black;
        }

        img {
          margin-right: 10px;
          height: 10px;
        }
      }
    }
  }
  .register {
    position: absolute;
    left: 50px;
    display: flex;
    flex-direction: row;

    .registerButton {
      border: none;
      // background-color: #00aa95;
      background-color: #1876d1;
      color: white;
      padding: 10px;
      width: 100px;
      border-radius: 10px;
      cursor: pointer;
      border: 1px solid white;
      transition: 0.3s;

      &:hover {
        background-color: white;
        color: #1876d1;
        border: 1px solid #1876d1;
      }
    }

    .loginButton {
      border: none;
      background-color: transparent;
      color: white;
      padding: 10px;
      width: 100px;
      border-radius: 10px;
      cursor: pointer;
      transition: 0.3s;
      margin-left: 10px;
      display: flex;
      justify-content: center;

      .arrow {
        height: 25px;
        width: 30px;
        margin-left: 5px;
      }

      &:hover {
        margin-left: 15px;
        // box-shadow: 2px 3px 3px #017d6e, 2px 3px 3px #017d6e;
      }
    }
  }
}
