.container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  .landing {
    background-color: #1876d1;
    display: flex;
    flex-direction: column;
    height: calc(100vh - 80px);
    // background-image: linear-gradient(#, white);
    // background-image: linear-gradient(#1876d1, #00aa95);
    // background-image: linear-gradient(#00aa95, white);

    .landSection {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      height: 700px;
    }

    .sloganSection {
      display: flex;
      flex-direction: column;
      width: 400px;
      margin-left: 150px;
      .slogan {
        color: white;
        font-size: 30px;
        font-weight: bold;
      }
      .subSlogan {
        margin-top: 10px;
        color: white;
        font-size: 20px;
      }

      .startContainer {
        display: flex;
        flex-direction: row;
        // justify-content: center;
        // align-items: center;
        .startInput {
          margin-top: 30px;
          border: none;
          border-radius: 0 10px 10px 0;
          height: 50px;
          width: 250px;
          padding-right: 10px;
        }

        .startButton {
          height: 52px;
          border: 1px solid white;
          color: white;
          border-radius: 10px 0 0 10px;
          background-color: #1876d1;
          cursor: pointer;
          transition: 0.4s;
          margin-top: 30px;
          width: 110px;

          &:hover {
            color: #1876d1;
            // border: 1px solid #1876d1;
            border-right: 1px solid #1876d1;
            background-color: white;
          }
        }
      }
    }
    .sloganImage {
      // height: 150px;
      // width: 150px;
    }
  }
}
